<template>
	<div id="header">
			<div class="headerBlock"><h1>Su AGE II team matcher</h1></div>
			<!-- <div class="headerBlock" id="firma"><h1><a href="../">Forka137</a></h1></div> -->
		</div>
		<div class="container">
			<div class="contentColumn">
				<h2>Seleccione los jugadores disponibles</h2>
				<div id="checkContainer">
					<label><input type="checkbox" class="cbox" value="matte">Matte</label>
					<label><input type="checkbox" class="cbox" value="leo">The Leo</label>
					<label><input type="checkbox" class="cbox" value="chewi">Chewi</label>
					<label><input type="checkbox" class="cbox" value="tepe">Tepe</label>
					<label><input type="checkbox" class="cbox" value="forka">Forka</label>
					<label><input type="checkbox" class="cbox" value="gyogon">Gyogon</label>
					<label><input type="checkbox" class="cbox" value="lara">Lara</label>
					<label><input type="checkbox" class="cbox" value="mefis">Mefis</label>
				</div>
				<button type="button" id="generate">Generar</button>
				<div id="teamContainer">
					<div class="teams">
						<h2>Equipo 1</h2>
						<ul id="team1">
						</ul>
					</div>
					<div class="teams">
						<h2>Equipo 2</h2>
						<ul id="team2">
						</ul>
					</div>
				</div>
				<div class="centerLine"></div>
				<div id="fairness">
					<div id="leftTeam"></div>
					<div id="rightTeam"></div>
				</div>
				<div class="centerLine"></div>
				<div id="remainder">
						<h2>Sobrante</h2>
						<ul id="noteam">
						</ul>
				</div>
			</div>
		</div>
</template>

<script>
	export default {
		name: "Column",
		mounted(){
			var generateButton = document.getElementById('generate'),
			teamOneBox = document.getElementById('team1'),
			teamTwoBox = document.getElementById('team2'),
			remainderBox = document.getElementById('remainder'),
			remainderList = document.getElementById('noteam'),
			leftTeamBar = document.getElementById('leftTeam'),
			rightTeamBar = document.getElementById('rightTeam');

			var isArray = Array.isArray || function(value) {
				return {}.toString.call(value) !== "[object Array]"
			};
			function Team() { //objeto team
				this.players = [];
				this.playerNames = [];
				this.points = 0.0;

				this.updateNames = function() {
					for (let i = 0; i < this.players.length; i++) {
						if(nameMap.has(this.players[i])) {
							this.playerNames.push(nameMap.get(this.players[i]));
							}
						} 
					}
				}

			function shuffleArray() {
				var arrLength = 0;
				var argsLength = arguments.length;
				var rnd, tmp;

				for (let index = 0; index < argsLength; index += 1) {
					if (!isArray(arguments[index])) {
						throw new TypeError("Argument is not an array.");
					}

					if (index === 0) {
						arrLength = arguments[0].length;
					}
					if (arrLength !== arguments[index].length) {
						throw new RangeError("Array lengths do not match.");
					}
				}
				while (arrLength) {
					rnd = Math.floor(Math.random() * arrLength);
					arrLength -= 1;
					for (let argsIndex = 0; argsIndex < argsLength; argsIndex += 1) {
						tmp = arguments[argsIndex][arrLength];
						arguments[argsIndex][arrLength] = arguments[argsIndex][rnd];
						arguments[argsIndex][rnd] = tmp;
					}
				}
			}

			function getAvailablePlayers() {
				let availablePlayers = [];
				let players = document.querySelectorAll('input[type=checkbox]:checked');

				for (let i = 0; i < players.length; i++) {
					availablePlayers.push(players[i].value)
				}

				return availablePlayers;
			}

			function getSkillPoints(players) {
				let skillPoints = []
				for (let i = 0; i < players.length; i++) {
					for (let j = 0; j < skillMap.size; j++) {
						if(skillMap.has(players[j])) {
							skillPoints.push(skillMap.get(players[i]));
							break;
						}
					}	
				}
				return skillPoints;
			}	

			function chooseTeams(players, skillPoints) {
				remainderBox.style.display = "none";
				let totalPoints = skillPoints.reduce(function(a, b) { return a + b; }, 0),
					halfPoints = totalPoints/2,
					teamOne = new Team(),
					teamTwo = new Team(),
					remainder = [],
					selectedTeams = [];

				for(let i = 0; i < players.length; i++){
					if(teamOne.points + skillPoints[i] < halfPoints) {
						teamOne.players.push(players[i]);
						teamOne.points += skillPoints[i]

					} else if(teamTwo.points + skillPoints[i] < halfPoints){
						teamTwo.players.push(players[i])
						teamTwo.points += skillPoints[i]
					} else {
						remainder.push(players[i])
					}
				}

				let pointsDifference = Math.abs(teamOne.points - teamTwo.points);

				if(remainder.length > 0 && pointsDifference > 0.25) {
					if(teamOne.points < teamTwo.points) {
						teamOne.players.push(remainder[0])
						teamOne.points += skillMap.get(remainder[0])
					} else {
						teamTwo.players.push(remainder[0])
						teamTwo.points += skillMap.get(remainder[0])
					}
				} else if(remainder.length > 0) {
					showRemainder(remainder[0]);
				}

				selectedTeams = [teamOne, teamTwo]
				return selectedTeams
			}

			function showRemainder(player) {
				remainderBox.style.display = "block";
				remainderList.innerHTML = ``;
				let playerName = nameMap.get(player);
				remainderList.innerHTML = `<li>${playerName}</li>`;
			}

			function insertTeams(teams) {
				teams[0].updateNames()
				teams[1].updateNames()
				teamOneBox.innerHTML = ``;
				teamTwoBox.innerHTML = ``;
				let toInsert = ``;

				teams[0].playerNames.forEach( function(player) {
					toInsert += `<li>${player}</li>`;
				});
				teamOneBox.innerHTML = toInsert;

				toInsert = ``;
				teams[1].playerNames.forEach( function(player) {
					toInsert += `<li>${player}</li>`;
				});
				teamTwoBox.innerHTML = toInsert; 
			}

			function updateBars(teams) {
				let totalPoints = teams[0].points + teams[1].points;
				let halfPoints = totalPoints / 2;
				let leftBarWidth = 50 * (teams[0].points/halfPoints);
				let rightBarWidth = 100 - leftBarWidth;
				leftTeamBar.style.width = `${leftBarWidth}%`;
				rightTeamBar.style.width = `${rightBarWidth}%`;
				console.log(leftBarWidth)
				console.log(rightBarWidth)
			}

			generateButton.addEventListener("click", () => {
				let players = getAvailablePlayers(),
					skillPoints = getSkillPoints(players),
					teams = [];

				shuffleArray(players, skillPoints)
				teams = chooseTeams(players, skillPoints)

				if(teams.length > 1) {
					insertTeams(teams);
					updateBars(teams);
				}

				//debug
				//console.log(teams)
			});

			var skillDict = {
				matte: 1.0,
				leo: 0.8,
				chewi:0.6,
				tepe:0.5333,
				forka:0.4666,
				gyogon:0.4,
				lara:0.3,
				mefis:0.2
			}

			var nameDict = {
				matte: "Matte",
				leo: "The Leo",
				chewi: "Chewi",
				tepe: "Tepe",
				forka: "Forka",
				gyogon: "Gyogon",
				lara: "Lara",
				mefis: "Mefis"
			}

			var skillMap = new Map(Object.entries(skillDict));
			var nameMap = new Map(Object.entries(nameDict));
		}
	}
</script>

<style scoped>	/*la palabra scope significa que el style es solo para este componente*/

.container{
	display: flex;
	justify-content: center;
}

h1{
	font-family: 'Libre Baskerville', serif;
	color: #FCD303;
	font-size: 2em;
	margin-block-start: 0em;
	margin-block-end: 0em;
	margin-inline-start: 0px;
	margin-inline-end: 0px;
	margin:10px 10px 10px 10px;
	font-weight: bold;
}
ul {
	color: #CCCCCC;
	text-align:center;
	list-style-type: none;
	margin-block-start: 2em;
	margin-block-end: 2em;
	margin-inline-start: 0px;
	margin-inline-end: 0px;
	padding-inline-start: 0px;
	font-size:20px;
}
.contentColumn {
	font-family:Helvetica, Arial, sans-serif;
	display: flex;
	color: white;
	flex-direction: column;
	align-items: center;
	width: 80vw;
}

#header{
	display: flex;
	justify-content: center;
	border-bottom:solid black;
	border-width:2px;
	height:60px;
}

#checkContainer{
	color: #CCCCCC;
	display: flex;
	justify-content: center;
	margin-bottom: 50px;
}

#teamContainer{
	display: flex;
	width: 100%;
	justify-content: space-around;
	height: 300px;
}

input[type='checkbox']{
	margin-left: 20px;
}

.headerBlock{
	float:left;
	height:60px;
	border-width:2px;
}

#fairness{
	display: flex;
	justify-content: center;
	width: 100%;
	height: 30px;
}

#leftTeam{
	height: 30px;
	width: 50%;
	background-color: #364FD8;
	z-index: -200;
}

#rightTeam{
	height: 30px;
	width: 50%;
	background-color: #47C84D;
	z-index: -100;
}

#remainder{
	display: none;
}

.centerLine{
	height: 10px;
	border-right: 2px solid;
	border-left: 2px solid;
	border-color: #FCD303;
}

</style>
